import * as React from 'react';
import './../css/Contact.css';

import git from './../assets/github.png';
import link from './../assets/linkedin.png';


class Contact extends React.Component {
    public render() {
        return (
            <div className="Contact">

                <div className="row">
                    <div className="col">
                        <h1>Contact 📱</h1>
                    </div>
                </div>

                <div className="row ">
                    <div className="col">
                        <a href="https://github.com/bunyu93" target="_blank">
                            <img className="infoSource rounded" src={git} alt="Github" />
                        </a>
                    </div>
                    <div className="col">
                        <a href="https://www.linkedin.com/in/bunyaminsakar/" target="_blank">
                            <img className="infoSource rounded" src={link} alt="Linkedin" />
                        </a>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;



