import * as React from 'react';
import './../css/Skills.css';


class Skills extends React.Component {
    public render() {
        return (
            <div className="Skills">
                <div className="row justify-content-center">
                    <h1>What I do 💻</h1>
                </div>

                <div className="row customRow">
                    <div className="col">
                        <h3>Front-end</h3>
                        <ul>
                            <li>CSS3</li>
                            <li>HTML5</li>
                            <li>Typescript</li>
                            <li>Reactjs</li>
                            <li>Bootstrap</li>
                        </ul>
                    </div>

                    <div className="col">
                        <h3>Back-end</h3>
                        <ul>
                            <li>C#</li>
                            <li>.Net Core</li>
                            <li>Entity framework</li>
                            <li>NoSql</li>
                        </ul>
                    </div>

                    <div className="col">
                        <h3>Applications</h3>
                        <ul>
                            <li>Xamarin</li>
                            <li>Xamarin.Forms</li>
                            <li>WinForms</li>
                            <li>WPF</li>
                        </ul>
                    </div>
                </div>

                <div className="row customRow">
                    <div className="col">
                        <h3>Cloud</h3>
                        <ul>
                            <li>Azure</li>
                            <li>Digital Ocean</li>
                        </ul>
                    </div>

                    <div className="col">
                        <h3>Tools</h3>
                        <ul>
                            <li>Visual Studio</li>
                            <li>Visual Studio Code</li>
                        </ul>
                    </div>

                    <div className="col">
                        <h3>Interests</h3>
                        <ul>
                            <li>F#</li>
                            <li>Artificial Intelligence</li>
                            <li>WebAssembly</li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

export default Skills;



