import * as React from 'react';
import './../css/About.css';

import person from './../assets/ik.jpg';

class About extends React.Component {
    public render() {
        return (
            <div className="About">
                <div className="header">
                    <img className="me shadow-lg rounded-circle"
                        src={person}
                        alt="Me"
                    />
                    <h1>😁</h1>
                    <h2>
                        Who am I ?
                    </h2>
                    <h2>
                        A Person with passion for development
                        and likes Forex trading.
                    </h2>
                </div>
            </div>
        );
    }
}

export default About;



