import * as React from 'react';
import Particles from './../../node_modules/particlesjs/dist/particles.min.js';
import './../css/App.css';
import About from './About';
import Contact from './Contact';
import Projects from './Projects';
import Skills from './Skills';

class App extends React.Component {
  constructor(props: any) {
    super(props);
  }

  public componentDidMount() {
    Particles.init({
      // color: ['#D70026', '#a4a4a4', '#404040', 'ff0000'],
      color: ['#FF3B3F', '#A9A9A9'],
      connectParticles: true,
      maxParticles: 100,
      minDistance: 150,
      responsive: [
        {
          breakpoint: 768,
          options: {
            connectParticles: true,
            maxParticles: 50,
          }
        }, {
          breakpoint: 425
          ,
          options: {
            connectParticles: true,
            maxParticles: 25,
          }
        }, {
          breakpoint: 320,
          options: {
            connectParticles: true,
            maxParticles: 10
            // disables particles.js
          }
        }
      ],
      selector: '.background',
      sizeVariations: 0,
    });
  }

  public render() {
    return (
      <div className="App">
        <canvas className="background" />
        <div>
          <About />
        </div>
        <div className="container">
          <Skills />
          <Projects />
          <Contact />
        </div>
      </div>
    );
  }
}

export default App;



